<template>
  <div class="wpass-manage-page">
    <sub-header :isShowLinkBack="false">
      <template slot="title">W패스 쇼핑몰 관리</template>
    </sub-header>
    <div class="page-content">
      <div class="banner">
        <img src="@/assets/images/common/setting/img_wpass_about_banner.png" alt="W패스 간편 로그인 정보는 고객님 휴대폰에서만 안전하게 저장됩니다." />
      </div>
      <div class="content">
        <div class="shopping-mall-item-component" v-for="(item, index) in wpassList" :key="index">
          <div class="thumb">
            <a @click="storeDetailLink(item.hostKey)">
              <img :src="item.logoUri" alt="" />
            </a>
          </div>
          <div class="info">
            <div class="text">
              <dl>
                <dt class="blind">브랜드명</dt>
                <dd class="brand-name">{{item.korName}}</dd>
                <dt class="blind">카테고리</dt>
                <dd class="category">{{item.category}}</dd>
              </dl>
              <a class="detail-link blind-text" @click="storeDetailLink(item.hostKey)">자세히보기</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios';
import $axios from '@/extensions/axios.js';
import SubHeader from '@/components/SubHeader.vue';

export default {
  name: 'home',
  components: {
    SubHeader
  },
  data() {
      return {
          wpassList : []
      };
  },
  created() {
    axios.all([
      this.getWpassList()
    ]).then(axios.spread((resWpassList) => {
      this.wpassList = resWpassList.data.list;
    }));
  },
  methods : {
    getWpassList() {
      return $axios({
          // url : 'http://localhost:3040/api/configure/v1',
          url : 'https://api.pre.wonder-shopping.com/v1/store/mall',
          method : 'get'
      });
    },

    storeDetailLink(hostKey) {
      const url = `/mypage/order?hostKey=${hostKey}`;
      this.$router.push(url);
    }
  }
}
</script>

