import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/mypage/claim',
      name: 'claimList',
      component: () => import('./views/mypage/claim/index.vue')
    },

    {
      path: '/mypage/order',
      name: 'orderList',
      component: () => import('./views/mypage/order/index.vue')
    }
  ]
});
